import zpRequest from "../index";

enum loginAPI {
  phoneUrl = "/login/cellphone", //
  stCodeUrl = "/captcha/sent", //
  yzCodeUrl = '/captcha/verify', //
  loginStatusUrl = "/login/status", //
  refreshLoginUrl = "/login/refresh", //
  logoutUrl = "/logout", //
}
/*  */
export function getPhone(params: any) {
  return zpRequest.post({
    url: loginAPI.phoneUrl,
    params: params,
  });
}
/*  */
export function getSentCode(phone: any) {
  return zpRequest.post({
    url: loginAPI.stCodeUrl,
    params: {
      phone,
    },
  });
}
/*  */
export function getYzCode(phone: any,captcha:any) {
  return zpRequest.post({
    url: loginAPI.yzCodeUrl,
    params: {
      phone,
      captcha
    },
  });
}
/*  */
export function getLoginStatus() {
  return zpRequest.get({
    url: loginAPI.loginStatusUrl,
  });
}

/*  */
export function getRefreshLogin() {
  return zpRequest.get({
    url: loginAPI.refreshLoginUrl,
  });
}

/*  */
export function getLogout() {
  return zpRequest.get({
    url: loginAPI.logoutUrl,
  });
}
